import Home from "@/views/Home";
import Blog from "@/views/Blog";
import About from "@/views/About";
import Message from "@/views/Message";
import Project from "@/views/Project";
import BlogDetail from "@/views/Blog/Detail";

export default [ // 
    {
        name: "Home",
        path: "/",
        component: Home,
        meta: {
            title: "",
        }
    }, //  /  Home 
    {
        name: "Blog",
        path: "/article",
        component: Blog,
        meta: {
            title: "",
        }
    },
    {
        name: "CategoryBlog",
        path: "/article/cate/:categoryId",
        component: Blog,
        meta: {
            title: "",
        }
    },
    {
        name: "BlogDetail",
        path: "/article/:id",
        component: BlogDetail,
        meta: {
            title: "",
        }
    },
    {
        name: "About",
        path: "/about",
        component: About,
        meta: {
            title: "",
        }
    },
    {
        name: "Message",
        path: "/message",
        component: Message,
        meta: {
            title: "",
        }
    },
    {
        name: "Project",
        path: "/project",
        component: Project,
        meta: {
            title: "&",
        }
    },
];