<template>
  <img 
    class="avater-container"
    :src="url" 
    :style="{
      width: size + 'px',
      height: size + 'px'
    }"
  />
</template>

<script>
export default {
    props: {
        url: {
            type: String, // url
            required: true, // 
        },
        size: {
            type: Number,
            default: 150,
        },
    }
}
</script>

<style scoped>
/* scoped  */
    .avater-container {
        border-radius: 50%;
        object-fit: cover;
        display: block;
    }
</style>